@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Regular.woff2") format("woff2"), url("../fonts/NeueMontreal-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Bold.woff2") format("woff2"), url("../fonts/NeueMontreal-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-Italic.woff2") format("woff2"), url("../fonts/NeueMontreal-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Neue Montreal";
  src: url("../fonts/NeueMontreal-BoldItalic.woff2") format("woff2"), url("../fonts/NeueMontreal-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "Antonia H1";
  src: url("../fonts/AntoniaH1-Bold.woff2") format("woff2"), url("../fonts/AntoniaH1-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Antonia H1";
  src: url("../fonts/AntoniaH1-BoldItalic.woff2") format("woff2"), url("../fonts/AntoniaH1-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}
body {
  font-family: "Neue Montreal", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #0C1A30;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Antonia H1", serif;
  font-weight: bold;
}

p {
  font-size: 1.25rem;
  font-weight: normal;
}

.wrap {
  max-width: 71.25rem;
  margin: 0 auto;
}

.page {
  margin: 0;
}

.btn {
  background: #ff6521;
  color: #ffffff;
  text-decoration: none;
  font-weight: normal;
  padding: 0.7rem 1.4rem;
}
.btn:visited {
  color: #ffffff;
}
.btn:hover {
  box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
  transition: 1s all;
}
.btn.btn-outline {
  background: transparent;
  border: 2px solid #ff6521;
  color: #ff6521;
}
.btn.btn-outline:visited {
  color: #ff6521;
}
.btn.btn-outline:hover {
  background: #ff6521;
  color: #ffffff;
  transition: 1s all;
}

.site-header {
  margin: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  position: relative;
}
@media screen and (max-width: 45.5rem) {
  .site-header {
    gap: 1rem;
  }
}
.site-header .or-logo {
  width: 10rem;
  max-width: 10rem;
  height: auto;
  fill: #0f3f3f;
}
.site-header .main-navigation .menu-toggle {
  margin-left: auto;
  background: transparent;
  border: none;
}
.site-header .main-navigation .menu-toggle svg {
  width: 2rem;
  height: auto;
}
.site-header .nav-menu {
  gap: 0.5rem 2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media screen and (max-width: 45.5rem) {
  .site-header .nav-menu {
    display: none;
    background-color: #e7ebbe;
    padding: 1.5rem;
    position: absolute;
    top: calc(100% + 2rem);
    right: 0;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
}
.site-header .nav-menu .menu-item a {
  color: #606A7A;
  text-decoration: none;
  font-size: 1.25rem;
  transition: 1s color;
}
.site-header .nav-menu .menu-item a:visited {
  color: #606A7A;
}
.site-header .nav-menu .menu-item a:hover {
  color: #ff6521;
  transition: 1s color;
}

.site-footer .footer-top {
  background: #0f3f3f;
  color: #C3C8D0;
  padding: 5rem 2rem 0;
  margin: 0 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top {
    padding: 2rem 2rem 0;
    margin: 0;
  }
}
.site-footer .footer-top .wrap {
  display: flex;
  gap: 2rem;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top .wrap {
    flex-direction: column-reverse;
  }
}
.site-footer .footer-top .wrap .footer-text {
  flex-basis: 0;
  flex-grow: 1;
}
.site-footer .footer-top .wrap .footer-text .footer-logo {
  width: 13.5rem;
  max-width: 13.5rem;
  height: auto;
  margin-bottom: 1rem;
  fill: #ffffff;
}
.site-footer .footer-top .wrap .footer-text .social-media-links a svg {
  width: 2rem;
  height: auto;
  fill: #bbde95;
  margin-right: 1rem;
  transition: 1s all;
}
.site-footer .footer-top .wrap .footer-text .social-media-links a:hover svg {
  fill: #ff6521;
  transition: 1s all;
}
.site-footer .footer-top .wrap .footer-menu {
  flex-basis: 0;
  flex-grow: 1;
  display: flex;
  gap: 2rem;
}
.site-footer .footer-top .wrap .footer-menu h2 {
  font-family: "Neue Montreal", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: #bbde95;
  font-size: 1.25rem;
  margin-top: 0;
}
.site-footer .footer-top .wrap .footer-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-footer .footer-top .wrap .footer-menu ul li {
  margin-bottom: 1rem;
}
.site-footer .footer-top .wrap .footer-menu ul li a {
  color: #F2F5F9;
  text-decoration: none;
  font-size: 1.25rem;
  transition: 1s color;
}
.site-footer .footer-top .wrap .footer-menu ul li a:visited {
  color: #F2F5F9;
}
.site-footer .footer-top .wrap .footer-menu ul li a:hover {
  color: #ff6521;
  transition: 1s color;
}
.site-footer .footer-top .or-footer {
  width: 100%;
  max-width: 71.25rem;
  fill: #F2F5F9;
  margin: 0 auto;
  display: block;
  opacity: 0.1;
  position: relative;
  bottom: -3px;
}
@media screen and (max-width: 45.5rem) {
  .site-footer .footer-top .or-footer {
    margin-top: 1.5rem;
  }
}
.site-footer .footer-bottom {
  margin: 0 2rem;
}
.site-footer .footer-bottom p {
  font-size: 0.75rem;
  color: #9198A4;
  margin: 0;
  padding: 0.5rem 0;
}