// COLOR DEFINITIONS

$color-orange: #ff6521;
$color-mint: #bbde95;
$color-laurel: #e7ebbe;
$color-deep-teal: #0f3f3f;

$color-white: #ffffff;
$color-bw-20: #F2F5F9;
$color-bw-40: #C3C8D0;
$color-bw-60: #9198A4;
$color-bw-80: #606A7A;
$color-bw-100: #0C1A30;

// TYPOGRAPHY

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Regular.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Bold.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-Italic.woff2') format('woff2'),
         url('../fonts/NeueMontreal-Italic.woff') format('woff');
	font-weight: normal;
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Neue Montreal';
	src: url('../fonts/NeueMontreal-BoldItalic.woff2') format('woff2'),
         url('../fonts/NeueMontreal-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic;
	font-display: swap;
}

@font-face {
	font-family: 'Antonia H1';
	src: url('../fonts/AntoniaH1-Bold.woff2') format('woff2'),
         url('../fonts/AntoniaH1-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Antonia H1';
	src: url('../fonts/AntoniaH1-BoldItalic.woff2') format('woff2'),
         url('../fonts/AntoniaH1-BoldItalic.woff') format('woff');
	font-weight: bold;
	font-style: italic;
	font-display: swap;
}

$font-heading: 'Antonia H1', serif;
$font-body: 'Neue Montreal', sans-serif;

$font-size-base: 16px;

// LAYOUT

$wrap-max: 71.25rem;

// BASE STYLES

body {
  font-family: $font-body;
  font-size: $font-size-base;
  line-height: 1.5;
  color: $color-bw-100;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-heading;
  font-weight: bold;
}

p {
    font-size: 1.25rem;
    font-weight: normal;
}

.wrap  {
  max-width: $wrap-max;
  margin: 0 auto;
}

.page {
    margin: 0;
}

// COMPONENT STYLES

// Buttons

.btn {
    background: $color-orange;
    color: $color-white;
    text-decoration: none;
    font-weight: normal;
    padding: 0.7rem 1.4rem;

    &:visited {
        color: $color-white;
    }

    &:hover {
        box-shadow: 0 10px 10px -8px rgba(0, 0, 0, 0.25);
        transition: 1s all;
    }

    &.btn-outline {
        background: transparent;
        border: 2px solid $color-orange;
        color: $color-orange;


        &:visited {
            color: $color-orange;
        }

        &:hover {
            background: $color-orange;
            color: $color-white;
            transition: 1s all;
        }
    }
}

.site-header {
    margin: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    position: relative;
    @media screen and (max-width: 45.5rem) {
        gap: 1rem
    }

    .or-logo {
        width: 10rem;
        max-width: 10rem;
        height: auto;
        fill: $color-deep-teal;
    }

    .main-navigation {

        .menu-toggle {
            margin-left: auto;
            background: transparent;
            border: none;

            svg {
                width: 2rem;
                height: auto;
            }
        }

    }

    .nav-menu {
        gap: 0.5rem 2rem;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        @media screen and (max-width: 45.5rem) {
            display: none;
            background-color: $color-laurel;
            padding: 1.5rem;
            position: absolute;
            top: calc(100% + 2rem);
            right: 0;
            gap: 1rem;
            flex-direction: column;
            align-items: center;
        }

        .menu-item a {
            color: $color-bw-80;
            text-decoration: none;
            font-size: 1.25rem;
            transition: 1s color;

            &:visited {
                color: $color-bw-80;
            }

            &:hover {
                color: $color-orange;
                transition: 1s color;
            }

        }
    }

}

.site-footer {

    .footer-top {
        background: $color-deep-teal;
        color: $color-bw-40;
        padding: 5rem 2rem 0;
        margin: 0 2rem;
        @media screen and (max-width: 45.5rem) {
            padding: 2rem 2rem 0;
            margin: 0;
        }

        .wrap {
            display: flex;
            gap: 2rem;
            @media screen and (max-width: 45.5rem) {
                flex-direction: column-reverse;
            }
    
            .footer-text {
                flex-basis: 0;
                flex-grow: 1;

                .footer-logo {
                    width: 13.5rem;
                    max-width: 13.5rem;
                    height: auto;
                    margin-bottom: 1rem;
                    fill: $color-white;
                }
        
                .social-media-links {
        
                    a {     
                        svg {
                            width: 2rem;
                            height: auto;
                            fill: $color-mint;
                            margin-right: 1rem;
                            transition: 1s all;
                        }
        
                        &:hover {
                            svg {
                                fill: $color-orange;
                                transition: 1s all;
                            }
                        }
                    }        
                }
            }

            .footer-menu {
                flex-basis: 0;
                flex-grow: 1;
                display: flex;
                gap: 2rem;

                h2 {
                    font-family: $font-body;
                    font-weight: bold;
                    text-transform: uppercase;
                    color: $color-mint;
                    font-size: 1.25rem;
                    margin-top: 0;
                }

                ul {
                    list-style: none;
                    padding: 0;
                    margin: 0;

                    li {
                        margin-bottom: 1rem;

                        a {
                            color: $color-bw-20;
                            text-decoration: none;
                            font-size: 1.25rem;
                            transition: 1s color;

                            &:visited {
                                color: $color-bw-20;
                            }

                            &:hover {
                                color: $color-orange;
                                transition: 1s color;
                            }
                        }
                    }
                }
            }
        }

        .or-footer {
            width: 100%;
            max-width: $wrap-max;
            fill: $color-bw-20;
            margin: 0 auto;
            display: block ;
            opacity: 0.1;
            position: relative;
            bottom: -3px;
            @media screen and (max-width: 45.5rem) {
                margin-top: 1.5rem;
            }
        }

    }

    .footer-bottom {
        margin: 0 2rem;
        p {
            font-size: 0.75rem;
            color: $color-bw-60;
            margin: 0;
            padding: 0.5rem 0;
        }
    }

}


